body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

:root{--background-color-app: #313131;--secondary-color: #414141;--third-color: #525252;--fourth-color: #ca3e47}.App{text-align:center;background-color:#313131;background-color:var(--background-color-app);height:400vh;display:grid;grid-template-areas:"header header header" "CardsSection CardsSection CardsSection" "MeSection MeSection MeSection" "footer footer footer";grid-template-rows:1fr 1fr 1fr 0.5fr;grid-template-columns:1fr;grid-gap:0px}.App header{grid-area:header}.App .CardsSection{grid-area:CardsSection}.App .MeSection{grid-area:MeSection}.App footer{grid-area:footer}

@font-face{font-family:"BarlowCondensed";src:url(/static/media/BarlowCondensed-ExtraBoldItalic.10bb4d02.ttf)}.App-logo{width:20rem;margin-top:5rem;pointer-events:none}.App-header{position:relative;min-height:50vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white;-webkit-animation:slide-down 1s ease;animation:slide-down 1s ease}.Container{align-items:center;width:100%;position:absolute;top:-5rem;display:flex;justify-content:center;flex-direction:column;background-color:white;height:30rem}.App-link{top:25rem;position:absolute;background-color:#fff;color:#ff5757;padding:0.5rem 4rem;text-decoration:none;transition:0.3s ease;transition-property:transform, border}.App-link:hover{transform:translateY(-5px);border:1px solid #000}.custom-shape-divider-top-1600866841{position:absolute;top:25rem;width:100%;overflow:hidden;line-height:0}.custom-shape-divider-top-1600866841 svg{position:relative;display:block;width:calc(100% + 1.3px);height:150px}.custom-shape-divider-top-1600866841 .shape-fill{fill:#ffffff}@-webkit-keyframes slide-down{0%{top:-70vh}100%{top:0}}@keyframes slide-down{0%{top:-70vh}100%{top:0}}

section.CardsSection{color:white}

section.MeSection{color:white}

